@import '../../common/assets/styles/_variables';

.goods-block{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
}

.good{
    border: 2px solid rgba(0,0,0,.15);
    border-radius: 15px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 200px;
    margin-bottom: 20px;
    &:hover{
        border: 2px solid $f_color;

    }
    img{
        width: 70%;
        height: 150px;
    }
    p{
        width: 100%;
        text-align: left;
        margin-bottom: 5px;
        span{
            font-weight: 700;
            color: $f_color;
            font-size: 18px;
        }
    }
    h4{
        width: 100%;
        text-align: left;
        font-weight: 400;
        font-size: 16px;
    }
    button{
        margin-top: 10px;
        width: 90%;
        border: none;
        border-radius: 20px;
        background-color: rgba($color: #D9D9D9, $alpha: .45);
        padding: 7px 8px;
        &:hover{
            background-color: $f_color;
            color: #fff;
            cursor: pointer;
        }
    }
    
    .stock-sign-con{
        position: relative;
        top: -26px;
        right: 86px;
    }
    
}

.img{
    .stock-sign-con{
        position: relative;
        top: -23px;
        right: -101px;
        .stock-sign{
            position: absolute;
        
        }
    } 

}
  