@import "../../assets/styles/_variables";

.shop-icon {
  color: $f_color;
}

.cart-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 85%;
  background-color: white;
  border: 2px solid #000;
  box-shadow: 24px;
  padding: 20px 40px;
  height: 90%;
  justify-content: center;
  .cart-title {
    text-align: center;
    width: 100%;
    font-size: 28px;
    color: $f_color;
    text-shadow: 1px 1px 2px #ccc;
    border-bottom: 1px solid $f_color;
  }
}

.cart-items {
  display: flex;
  justify-content: center;
  width: 100%;
  text-align: center;
  flex-direction: column;
  align-items: center;
}

.cart-table {
  display: flex;
  margin: 10px 0;
  max-height: 780px;
  max-width: 100%;
  text-align: center;
}


