@import "../../common/assets/styles/_variables";

.basket {
  width: 100%;
  margin-top: 20px;
}
.cart-item {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  width: 100%;
  margin-bottom: 20px;
  .cart-num {
    width: 5%;
    border: 1px solid #ccc;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 20px;
  }
  .cart-info {
    width: 94%;
    border-radius: 15px;
    border: 1px solid #ccc;
    display: flex;
    flex-direction: row;
    padding: 10px 20px;
    .img {
      width: 20%;
      img {
        width: 100px;
        margin-right: 10px;
      }
    }

    .desc {
      margin-top: 10px;
      width: 60%;
      text-align: left;
      p {
        font-size: 14px;
        margin: 5px 0;
      }
      h5 {
        color: $f_color;
        font-weight: 700;
        font-size: 15px;
      }
    }

    .actions {
      margin-top: 10px;
      width: 20%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .price {
        font-weight: 700;
        color: $f_color;
        text-align: right;
        width: 100%;
        font-size: 20px;
        .stock-title{
          color: #797979;
          font-size: 12px;
        }
        span {
          color: #797979;
          font-size: 14px;
        }
      }

      .button-group {
        display: flex;
        flex-direction: row;
        .btn-gr {
          background-color: #ccc;
          padding: 0;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-around;
          width: 75%;
          margin-right: 10px;
          border-radius: 20px;
          font-size: 18px;
          span {
            font-size: 14px;
            padding-bottom: 2px;
          }
          input {
            width: 20%;
            text-align: center;
            margin: 0;
            background: transparent;
            outline: none;
            border: none;
          }

          input::-webkit-outer-spin-button,
          input::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
            display: none;
          }

          /* Firefox */
          input[type="number"] {
            -moz-appearance: textfield;
          }
          button,
          button:focus {
            cursor: pointer;
            height: 100%;
            width: 40%;
            outline: none;
            background: transparent;
            border: 1px solid transparent;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            svg {
              width: 40%;
            }
          }

          button:active {
            outline: none;
            background: transparent;
            // border-left: 1px solid grey;
            // border: 1px solid grey;
            border-radius: 20px;
          }
        }
        .delete-btn {
          width: 20%;
          background: none;
          color: $f_color;
          border: none;
          font-size: 18px;
          display: flex;
          align-items: center;
          & :hover {
            color: rgb(146, 146, 146);
          }
        }
      }
    }
  }
}

.cart-info-bottom {
  display: flex;
  width: 100%;
  height: 90px;
  align-items: center;
  border: 1px solid $f_color;
  padding: 40px;
  border-radius: 10px;
  font-weight: 400;
  font-size: 20px;
  justify-content: space-between;

  .right{
    text-align: left;
    span{
      color: $f_color;
    }
  }
}

.cart-info-bottom span {
  color: $f_color;
  font-weight: 700;
}

.btn-order {
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 40px;
    background-color: $f_color;
    border: 1px solid transparent;
    color: #fff;
    outline: none;
    border-radius: 14px;
    font-size: 15px;
    font-weight: 700;
    transition: ease 0.25s;
    cursor: pointer;
    &:hover {
      background-color: #fff;
      color: #000;
      border: 2px solid $f_color;
    }
  }
}
