@import '../../common/assets/styles/_variables';

.good-container{
  margin: 20px 0;

  .good-head{
    width: 100%;
    display: flex;
    flex-direction: row;

    .good-images{
      width: 50%;
    }

    .good-info{
      width: 50%;

      p{
        font-size: 18px;
        text-align: justify;
        line-height: 24px;
      }

      div{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 15px;
        
        h3{
          font-size: 28px;
          font-weight: 700;
        }

        span{
          color: $f_color;
          font-size: 26px;
          font-weight: 700;
        }
      }

      .short-desc{
        margin-bottom: 10px;
      }
      h4{
        color: $f_color;
        margin: 25px 0 10px;
        font-size: 20px;
      }

      ol{
        list-style: none;
        li{
          display: flex;
          justify-content: space-between;

          div{
            margin-bottom: 8px;
          }
        }

        li div:nth-child(3) {
          text-align: right;
        }

        li div:nth-child(2) {
          flex: 1 0;
          border-bottom: 2px dotted #000;
          height: 1em;
          margin: 0 .4em;
        }
      }
    }
  }

  .good-body{
    h4{
      margin-bottom: 10px;
      color: $f_color;
      font-size: 20px;
    }

    p{
      font-size: 18px;
      text-align: justify;
      line-height: 24px;
    }
  }

  
}