@import "../../common/assets/styles/_variables";
/*-------------     HEAD            ------------------*/
.head {
  background-size: cover;
  background-repeat: no-repeat;
  // background-attachment: fixed;
  background-position: center;
  color: white;
  padding-top: 20px;

  a {
    color: white;
  }

  .menu-logo {
    width: 100%;
  }
  .about {
    height: 600px;
  }

  .container {
    flex-direction: column;
  }
}

.logo {
  img {
    width: 200px;
  }
}

.menu {
  margin-top: 1em;
  font-weight: 600;
  font-size: 1.1vw;
  -webkit-transition: 0.25s;
  transition: 0.25s;
  li {
    margin-left: 2em;
    -webkit-transition: 0.25s;
    transition: 0.25s;
    border-bottom: 3px solid transparent;
    &:hover {
      border-bottom: 3px solid $f_color;
      -webkit-transition: 0.25s;
      transition: 0.25s;
    }
  }
}

.about {
  justify-content: center;
  font-size: 18px;
  h1 {
    font-size: 45px;
  }

  .descs {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 20px;
    .right {
      display: flex;
      width: 30%;
    }
    .left {
      display: flex;
      width: 30%;
      margin-right: 20px;
    }
  }

  .main-desc {
    width: 40%;
    font-size: 24px;
    text-align: center;
    margin-bottom: 1em;
    color: $f_color;
    margin: 20px 0;
  }

  .head-btns {
    display: "flex";
    width: "100%";
    flex-direction: "row";
    text-align: "center";
    justify-content: "center";
    margin-top: "50px";
    a button {
      margin-bottom: 20px;
    }
  }
}
