@import "../../common/assets/styles/_variables";

.page-container{
  width: 100%;
  height: 650px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 100px;
  .logo-login{
    margin-bottom: 30px;
  }

  input{
    width: 400px;
    margin-bottom: 10px;
    padding: 5px 15px;
    background-color: transparent;
    border: 2px solid $f_color;
    border-radius: 20px;
    font-size: 16px;
    &:hover, &:focus{
      outline: none;
    }
  }

  .buttons{
    margin-top: 10px;
    display: flex;
    width: 50%;
    text-align: center;
    justify-content: center;
    align-items: center;
    button{
      width: 30%;
      text-align: center;
      border: none;
      background-color: $f_color;
      color: #fff;
      height: 35px;
      cursor: pointer;
      border-radius: 20px;
      font-size: 15px;
      &:hover{
        color: #000;
        border: 2px solid $f_color;
        background-color: transparent;
      }
    }
  }

  .regBtn{
    width: 30%;
    margin-right: 10px;
    button{
      width: 100%;
    }
  }
}